body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  overflow: hidden;
  width: 100%;
  height: 500vh;
  font-family: Futura, sans-serif;
  color: white;
}

.slidingBackground{
  background: url(/frame-react-home/static/media/6f6ccb4c.dac30a96.png) repeat;
  z-index: -11;
  width: 100%;
  height: 1000vh;
  position: absolute;
  bottom: -500vh;

  /* animation: slide 250s linear infinite; */
}

.topNav {
  display: flex;
  position: absolute;
  right: 0;
  top: 0;
}

.navItem {
  margin-top: 1em;
  margin-right: 1em;
}

.logoContainer {
  position: absolute;
  width: 20em;
  top: 25vh;
  left: 0;
  right: 0;
  margin: auto;
}

.centerLogo {
  width: 20em;
}

.logoBreak {
  margin: auto;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  margin-top: -2.3em;
  width: 19em;
  border: 2px solid white;
}

.logoSub {
  width: 18em;
  margin: 0 auto;
}

.logoSub h2 {
  margin-top: -1em
}

.landingButtons {
  position: absolute;
  left: 0;
  right: 0;
  top: 52vh;
  margin: auto;
  margin-top: 6em;
}

.signUpButton {
  margin: 1em auto;
  border-radius: .4em;
  width: 13em;
  border: 4px solid white;
}

.downPoint {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 3em;
}

.particleScreen {
  position:fixed;
  width: 100% !important;
  height: 100vh !important;
  z-index: -10;
}

/* .particleScreen canvas {
  width: 100% !important;
  height: 500vh !important;
} */

@-webkit-keyframes slide {
  0%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  100%{
    -webkit-transform: translate3d(0, 1692px, 0);
            transform: translate3d(0, 1692px, 0); /* The image width */
  }
}

@keyframes slide {
  0%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  100%{
    -webkit-transform: translate3d(0, 1692px, 0);
            transform: translate3d(0, 1692px, 0); /* The image width */
  }
}
